"use client";

import React from "react";
import { useState, useEffect, useRef } from "react";
import { FaUserCircle } from "react-icons/fa";

export default function AccountPage() {
  const [user, setUser] = useState({
    name: "Abdo Saada",
    email: "abdelkfrxs@gmail.com",
    requests: 2,
    photo: null,
  });

  const [menuOpen, setMenuOpen] = useState(false);
  const menuRef = useRef(null);

  // إغلاق المنيو لما نضغط براها
  useEffect(() => {
    const handler = (e) => {
      if (menuRef.current && !menuRef.current.contains(e.target)) {
        setMenuOpen(false);
      }
    };
    document.addEventListener("mousedown", handler);
    return () => document.removeEventListener("mousedown", handler);
  }, []);

  // رفع صورة البروفايل
  const handlePhotoUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const photoURL = URL.createObjectURL(file);
      setUser((prev) => ({ ...prev, photo: photoURL }));
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 text-gray-800">
      {/* Navbar */}
      <div className="flex items-center justify-between px-6 py-3 bg-white shadow">
        <div className="text-lg font-bold text-green-600">logo</div>
        <div className="flex items-center gap-4">
          <input
            type="text"
            placeholder="Find the perfect service you need"
            className="px-3 py-1 border rounded-md w-72"
          />
          <button className="bg-green-500 text-white px-4 py-1 rounded">
            Search
          </button>
          <span className="cursor-pointer">Requests</span>
          <span className="cursor-pointer">Inbox</span>

          {/* Account dropdown */}
          <div className="relative" ref={menuRef}>
            <button
              onClick={() => setMenuOpen(!menuOpen)}
              className="font-medium"
            >
              {user.name} ▼
            </button>
            {menuOpen && (
              <div className="absolute right-0 mt-2 w-36 bg-white rounded shadow z-20">
                <div className="px-4 py-2 hover:bg-gray-100 cursor-pointer">
                  Account
                </div>
                <div className="px-4 py-2 hover:bg-gray-100 cursor-pointer">
                  Support
                </div>
                <div className="px-4 py-2 hover:bg-gray-100 cursor-pointer">
                  Log out
                </div>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* Main content */}
      <div className="flex p-6">
        {/* Sidebar */}
        <div className="bg-white rounded shadow p-4 w-1/4 text-center">
          {user.photo ? (
            <img
              src={user.photo}
              alt="Profile"
              className="w-24 h-24 rounded-full mx-auto object-cover"
            />
          ) : (
            <FaUserCircle className="text-7xl mx-auto text-gray-400" />
          )}
          <h2 className="font-semibold mt-2">{user.name}</h2>
          <p className="text-sm text-gray-600">{user.email}</p>
          <p className="text-sm text-gray-600">{user.requests} requests</p>

          <label className="mt-3 inline-block cursor-pointer text-sm text-blue-600 hover:underline">
            Upload photo
            <input
              type="file"
              className="hidden"
              accept="image/*"
              onChange={handlePhotoUpload}
            />
          </label>
        </div>

        {/* Content boxes */}
        <div className="ml-6 flex-1 space-y-4">
          <InfoBox
            title="Personal info"
            desc="Provide personal details and how we can reach you"
          />
          <InfoBox
            title="Requests"
            desc="Manage your requests and communications with pros"
          />
          <InfoBox
            title="Notifications"
            desc="Choose notification preferences and how you want to be contacted"
          />
          <InfoBox
            title="Inbox"
            desc="A space to view all your service updates, messages, and bookings in one place"
          />
        </div>
      </div>

      {/* Footer */}
      <div className="text-sm text-gray-500 text-center py-6 border-t">
        2025 Lilason, Inc. –{" "}
        <a href="#" className="underline">
          Terms of Use
        </a>{" "}
        –
        <a href="#" className="underline">
          {" "}
          Privacy Policy
        </a>{" "}
        –
        <a href="#" className="underline">
          {" "}
          Accessibility
        </a>{" "}
        –
        <a href="#" className="underline">
          {" "}
          Service Code
        </a>
      </div>
    </div>
  );
}

function InfoBox({ title, desc }) {
  return (
    <div className="bg-white p-4 rounded shadow cursor-pointer hover:bg-gray-50">
      <div className="flex justify-between items-center">
        <h3 className="font-semibold">{title}</h3>
        <span className="text-xl">›</span>
      </div>
      <p className="text-sm text-gray-600 mt-1">{desc}</p>
      <p></p>
    </div>
  );
}
